import Vue from 'vue'
import VueI18n from 'vue-i18n'
import App from './app.vue'

Vue.use(VueI18n);

const i18n = new VueI18n({
    locale: 'ko'
});

Vue.config.debug = true;
Vue.config.devtools = true;

new Vue({
    i18n,
    el: '#app',
    render: h => h(App)
});



